.Left {
    display: flex;
    margin-right: auto;
}
.Left div {
    margin-right: 2rem;
}
.Cancel {
    cursor: pointer;
}
.Cancel:hover,
.Cancel:active {
    text-decoration: underline;
}
.HeaderRow {
    display: flex;
    color: #fff;
    align-items: center;
}
.Tick {
    font-size: 3rem;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    color: #fff;
}
